.search-bar {
    display: grid;
    .search-bar-wrapper {
        display: grid;
        grid-row-gap: 23px;
        width: 100%;


        input {
            padding-left: 30px;
            color: $color-gray-one;
            box-shadow: 2px 3px 20px 1px rgba(0, 0, 0, 0.1);
            border: none;
            border-radius: 5px;
    
            transition: all .2s ease;
    
            &::placeholder {
                color: $color-gray-two;
                font-family: 'Font Awesome\ 5 Free', 'Lato', 'Arial', Arial, Helvetica, sans-serif;
                font-weight: 600;
            }
    
            &:focus {
                outline: none;
                box-shadow: 2px 3px 20px 1px rgba(0, 0, 0, 0.3);
            }
        }

        p {
            text-align: right;
            font-size: 14px;
            color: $color-gray-two
        }
    }
}

.search-bar-home {
    align-content: center;
    justify-items: center;
    input {
        height: 118px;
        font-size: 40px;
    }
}

.search-bar-results {
    align-content: start;
    justify-items: center;
    input {
        font-size: 22px;
        height: 36px;
    }
}